import { FETCH_ALL, FETCH_BY_SEARCH, FETCH_POST, CREATE, UPDATE, DELETE, START_LOADING, END_LOADING } from '../constants/actionTypes';

const postsReducer = (state = { isLoading: true, posts: [] }, action) => {

    switch (action.type) {
        case START_LOADING:
            return {
             ...state,
                isLoading: true
            }
        case END_LOADING:
            return {
              ...state,
                isLoading: false
            }
        case FETCH_ALL:
            return {
                ...state,
                posts : action.payload.data,
                currentPage: action.payload.currentPage,
                numberOfPages: action.payload.numberOfPages
            }
        case FETCH_POST:
            return {
               ...state,
                 post: action.payload
            }
        case FETCH_BY_SEARCH:
            return {...state, posts: action.payload.data}
        case CREATE:
            console.log('Reducer received CREATE action: ', action.payload)
            return { ...state, posts: [ ...state.posts, action.payload]};
        case UPDATE:
            console.log('Update recevied from edit button')
            return { ...state, posts: state.posts.map((post) => post._id === action.payload._id ? action.payload : post)};
        case DELETE:
            console.log('Delete received from delete button');
            return { ...state, posts: state.posts.filter((post) => post._id !== action.payload)};

        default:
            return state;
    }
};

export default postsReducer;